import * as types from "./types"
import Storage from "./storage"
import { id } from "./utils"

const today = new Date().toDateString()
const DB = new Storage({
  name: "yatl",
  store: [
    {
      name: "todos",
      key: "id"
    },
    {
      name: "late",
      key: "id"
    }
  ]
})

export const init = () => async dispatch => {
  await DB.init()

  const [active, late] = await Promise.all([
    DB.getAll("todos"),
    DB.getAll("late")
  ])

  const todos = [],
    newLates = []

  active.forEach(todo => {
    if (todo.category === "Today" && todo.date !== today) {
      newLates.push(todo)
    } else {
      todos.push(todo)
    }
  })

  dispatch({
    type: types.INITIAL_LOAD,
    todos: todos.reverse(),
    late: [...newLates, ...late].reverse()
  })

  DB.save("late", newLates)
}

export const delete_todo = (ids, store = "todos") => dispatch => {
  DB.delete(store, ids).then(() => {
    dispatch({
      type: types.REMOVE_TODO,
      store,
      ids
    })
  })
}

export const add_todo = (todos, store = "todos") => dispatch => {
  DB.save(store, todos).then(() => {
    dispatch({
      type: types.ADD_TODO,
      store,
      todos
    })
  })
}

export const create_todo = values => dispatch => {
  const todo = {
    ...values,
    id: id(),
    date: today
  }

  if (values.category === "") {
    todo.category = "Today"
  }

  dispatch(add_todo(todo))
}

export const update_todo = (updated, store = "todos") => dispatch => {
  DB.update(store, updated).then(() => {
    dispatch({
      type: types.UPDATE_TODO,
      updated,
      store
    })
  })
}

export const to_complete = ids => (dispatch, getState) => {
  const todos = getState().todos.find(task => ids.includes(task.id))
  const completed = todos.map(todo => {
    todo.date = new Date()
    return todo
  })

  Promise.all([
    dispatch(add_todo(completed, "complete")),
    dispatch(delete_todo(ids, "todos"))
  ])
}

export const edit_todo = todo => dispatch => {
  dispatch({
    type: types.EDIT_TODO,
    todo
  })
}



